<template lang="pug">

	#app(:class="darkTheme ? 'dark' : 'light'")
		
		
		header.header

			.container
				.header__inner

					h1.header__name {{ $t('firstname') }} {{ $t('lastname') }}

					div.theme
						input(type="checkbox",
							  v-model="darkTheme",
							  :aria-checked="darkTheme",
							  :aria-label="$t('changeTheme')")#themeCheckbox.theme__input
						label(for="themeCheckbox").theme__label
							img(alt="",
								src="data:image/png;base64,",
								class="theme__icon")
							img(alt="",
								src="data:image/png;base64,",
								class="theme__icon")
				br
				span {{ $t('work') }}
					b: a(href="https://media.1plus1.ua/" target="_blank") media.1plus1.ua
				br
				span {{ $t('push') }}
					b: a(href="https://twitter.com/search?q=%23webUserCare" target="_blank") #webUserCare
					span {{ $t('and') }}
					|
					b: a(href="https://twitter.com/search?q=%23%D0%B2%D0%B5%D0%B1%D1%82%D1%83%D1%80%D0%B1%D0%BE%D1%82%D0%B0" target="_blank") #вебтурбота
					span {{ $t('principles') }}

		.container
			main.main
				section.about
					h2 {{ $t('about') }}
					div
						img(src="../img/igor-stupka.jpg", :alt="$t('firstname') + ' ' + $t('lastname')").about__photo
						p {{ $t('about-text') }}
						p {{ $t('contact') }}
							b: a(href="mailto:igorstupka1@gmail.com") email
							| {{ $t('orAddMe') }}
							b: a(href="tg://resolve?domain=igor-stop") Telegram
							| .



				section.contacts
					h2 {{ $t('meet') }}
					ul.contacts__list
						li.contacts__place
							h3: a(href="https://twitter.com/igor_stop" target="_blank") Twitter ({{ $t('personal') }})
							p {{ $t('tw1') }}
						li.contacts__place
							h3: a(href="https://twitter.com/EatPrayFrontend" target="_blank") Twitter ({{ $t('aboutFrnd') }})
							p {{ $t('tw2') }}
						li.contacts__place
							h3: a(href="https://github.com/igor-stupka/" target="_blank") GitHub
							p {{ $t('gh') }}
						li.contacts__place
							h3: a(href="https://codepen.io/i-stupka/" target="_blank") CodePen
							p {{ $t('cp') }}
						li.contacts__place
							h3: a(href="https://www.instagram.com/igor_stop/" target="_blank") Instagram
							p {{ $t('ig') }}
						<!--li.contacts__place-->
							<!--h3: a(href="https://www.linkedin.com/in/igor-stupka-500868154/" target="_blank") LinkedIn-->
							<!--p {{ $t('li') }}-->

			.lang
				ul.lang__list
					li(v-for="(t, langName) in $i18n.messages"
					   class="lang__item")
						input(type="radio"
						v-model="I18N.locale"
						@change="changeLanguage"
						:value="langName"
						:id="'input-' + langName"
						class="lang__input")

						label(:for="'input-' + langName" 
							  class="lang__label") {{ langName }}


</template>

<script>
	import I18N from './multilang';

	export default {
		name: 'app',
		data () {
			return {
				darkTheme: false,
				I18N
			}
		},
		methods: {
			loadTheme() {
				let darkOS = window.matchMedia('(prefers-color-scheme: dark)').matches; // dark OS theme

				if (darkOS) {
					this.darkTheme = true;
				} else {
					// use previously theme
					if (localStorage.darkTheme)
						this.darkTheme = (localStorage.darkTheme === 'true');
				}
				this.themeMetaTagChanger();
			},
			themeMetaTagChanger() {
				let metaThemes = [...document.querySelectorAll('.meta-theme-color')];

				metaThemes.forEach(meta => {
					meta.setAttribute('content', this.darkTheme ? '#282c35' :'#fff')
				});
			},
			loadLanguage() {
				if (localStorage.language) {
					this.I18N.locale = localStorage.language;
				} else {
					if (navigator) {
					    let browserLang = navigator.language.substr(0, 2).toLowerCase();
						for (let lang in this.I18N.messages) {
							if (browserLang === lang) this.I18N.locale = browserLang;
						}
					}
				}
				this.setLangAttr();
			},
			changeLanguage() {
				localStorage.language = this.I18N.locale;
				this.setLangAttr();
			},
			setLangAttr() {
				document.documentElement.setAttribute('lang', this.I18N.locale)
			},
			reduceMotions() {
				const ReduceMotionsMatches = window.matchMedia('(prefers-reduced-motion: reduce)').matches;
				if (ReduceMotionsMatches) document.body.classList.add('reduce-motions');
			},
			checkBatteryLevel() {
				if (navigator.getBattery) {
					navigator.getBattery().then(function(battery) {
						let currentLevel = Math.round(battery.level * 100);

						if (currentLevel <= 10) {
							alert('Your battery level is ' + currentLevel + '%. Take care about it.');
						}
					})
				}
			}
		},
		mounted() {
			this.loadLanguage();
			this.loadTheme();
			this.reduceMotions();
			this.checkBatteryLevel();
		},
		watch: {
			darkTheme(newTheme) {
				// add selected theme to localStorage
				localStorage.darkTheme = newTheme;
				this.themeMetaTagChanger();
			}
		}
	}


</script>

<style lang="sass">
@import '../style/app'
</style>



